.App {
  padding: 30px;
  display: flex;
}

.App > div {
  margin-right: 50px;
}

.error {
  border: red 1px solid;
}

.error-message {
  color: red;
}

.active-filter {
  background-color: rgb(200, 83, 48);
  border-radius: 5px;
}

.is-done {
  opacity: 0.3;
}

.tasks-buttons {
  border-radius: 5px;
}
.remove-buttons {
  color: #f4472d;
  margin-left: 4px;
  font-size: 14px;
  margin-top: 5px;
  padding: 3px;
}

.tasks-counter {
  font-size: 0.7rem;
}

.checked {
  font-family: "Courier New", Courier, monospace;
  color: red;
  border: 5px inset red;
  background-color: #f4472d;
}
